import PropTypes from 'prop-types';
import React from 'react';
import { useField } from 'formik';

import { mapDynamicFieldToName } from '@mssgme/shared';
import { FormField, Input, Textarea, SelectSimple, RadioGroup, CheckboxGroup, CountrySelect } from '@mssgme/ui';

import { useCustomInputTheme, useInputTheme } from '../../hooks';

import styles from './DynamicFormInput.scss';

const specificComponents = {
    select: SelectSimple,
    radio: RadioGroup,
    checkbox: CheckboxGroup,
    country: CountrySelect,
};

const inputSpecificTypes = {
    name: 'text',
    phone: 'tel',
};

const handleEnterKey = (e) => {
    if (e.key === 'Enter') {
        e.preventDefault();
    }
};

const getFieldProps = (type, maxLength, optionsString) => {
    const component = specificComponents[type];

    if (component) {
        const options = optionsString ? optionsString.split(/\r?\n/).filter((i) => i).map((i) => ({ label: i, value: i })) : null;

        return {
            component,
            options,
        };
    } else {
        const isTextArea = maxLength > 250 && type === 'text';

        if (isTextArea) {
            return {
                component: Textarea,
                maxLength,
            };
        }

        return {
            component: Input,
            maxLength: ['date', 'time', 'number'].includes(type) ? null : maxLength,
            type: inputSpecificTypes[type] || type,
            onKeyPress: handleEnterKey,
            ...(type === 'number' && { min: 0, inputMode: 'numeric', pattern: '[0-9]*' }),
        };
    }
};

export const DynamicFormInput = ({ index, input, as, defaultMaxSize, theme }) => {
    const defaultStyle = useInputTheme(theme);
    const customStyle = useCustomInputTheme(theme);
    const style = customStyle || defaultStyle;
    const maxLength = input.size || defaultMaxSize;
    const name = mapDynamicFieldToName(input, index);
    const fieldProps = { name, ...getFieldProps(input.type, maxLength, input.options) };
    const [field, meta] = useField(fieldProps);

    return (
        <FormField
            shakeError
            as={as}
            name={name}
            errors={meta.touched && meta.error}
            label={input.label || input.type}
            style={style.input}
            errorStyle={style.error}
            inputClassName={styles.input}
            labelClassName={styles.label}
            required={input.required}
            {...field}
            {...fieldProps}
            {...(['radio', 'checkbox'].includes(input.type) && { userWebsiteField: true })}
        />
    );
};

DynamicFormInput.propTypes = {
    input: PropTypes.shape({
        _id: PropTypes.string,
        label: PropTypes.string,
        required: PropTypes.bool,
        size: PropTypes.number,
        options: PropTypes.string,
        type: PropTypes.string,
    }).isRequired,
    index: PropTypes.number.isRequired,
    as: PropTypes.oneOfType([PropTypes.string, PropTypes.elementType]),
    type: PropTypes.string,
    defaultMaxSize: PropTypes.number.isRequired,
    theme: PropTypes.object,
};
